import React from 'react'
import ReactQuill from 'react-quill';
import styled from 'styled-components'

import 'react-quill/dist/quill.snow.css'

const EditorStyled = styled.div`
  font-size: 1rem;
  flex-grow: 1;

  overflow: auto;
`

const modules = {
  syntax: true,
  toolbar: [
    [{ 'header': [1, 2, 3, false] }],
    ['bold', 'italic', 'underline','strike', 'blockquote'],
    [{'list': 'ordered'}, {'list': 'bullet'}, {'indent': '-1'}, {'indent': '+1'}],
    ['link', 'image'],
    ['code-block'],
    ['clean']
  ],
}

const formats = [
  'header',
  'bold', 'italic', 'underline', 'strike', 'blockquote',
  'list', 'bullet', 'indent',
  'link', 'image',
  'code-block'
]

const Editor = ({ text }) => {
  return (
    <ReactQuill theme="snow" ref={text} modules={modules} formats={formats}>
      <EditorStyled/>
    </ReactQuill>
  )
}

export default Editor