import React, { useEffect, useRef } from 'react'

import JournalBar from '../components/JournalBar'
import Editor from '../components/Editor'

import entryService from '../services/entries'

const JournalPage = ({ date }) => {
  const text = useRef() 

  useEffect(() => {
    (async () => {
      try {
        const loadedEntry = await entryService.get(date)
        if (loadedEntry !== undefined)
          text.current.getEditor().setContents(loadedEntry)
      } catch(exception) {
        console.error(exception)
      }
    })()
  }, [date])

  const saveEntry = async () => {
    try {
      await entryService.post(date, text.current.getEditor().getContents())
    } catch(exception) {
      console.error(exception)
    }
  }

  return (
    <>
    <JournalBar date={date} saveEntry={saveEntry}/>
    <Editor text={text}/>
    </>
  )
}

export default JournalPage