import React from 'react'
import { useHistory } from "react-router-dom";
import styled from 'styled-components'

import { NavigationButton, NavigationSpacer, NavigationBar, SideWrapper } from './shared/NavigationElements'
import colors from '../constants/colors'

const DateWrapper = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: baseline;
  margin: 0 1.8rem;

  min-width: 179px;
`

const Day = styled.span`
  font-weight: bold;
  color: ${colors.navbarElemColor};
  font-size: 1.7rem;
  margin-right: 0.4rem;
`

const Month = styled.span`
  font-weight: bold;
  color: ${colors.navbarElemColor};
  font-size: 1.7rem;
  margin-right: 0.5rem;
`

const Year = styled.span`
  font-weight: bold;
  color: ${colors.navbarElemColor};
  font-size: 1rem;
`

const JournalBar = ({ date, saveEntry }) => {
  let history = useHistory()

  const handleBack = (e) => {
    e.preventDefault()
    history.goBack()
  }

  return (
    <NavigationBar>
      <SideWrapper>
        <NavigationButton onClick={handleBack}>arrow_back</NavigationButton>
        <NavigationSpacer>settings</NavigationSpacer>
        <NavigationSpacer>save</NavigationSpacer>
      </SideWrapper>
      <DateWrapper>
        <Day>{date.getDate()}</Day>
        <Month>{date.toLocaleString('default', { month: 'long' })}</Month>
        <Year>{date.getFullYear()}</Year>
      </DateWrapper>
      <SideWrapper>
        <NavigationSpacer>arrow_back</NavigationSpacer>
        <NavigationButton onClick={saveEntry}>save</NavigationButton>
        <NavigationButton>settings</NavigationButton>
      </SideWrapper>
    </NavigationBar>
  )
}

export default JournalBar