import React from 'react'
import ReactDOM from 'react-dom'
import App from './App'

import { BrowserRouter as Router } from 'react-router-dom'
import { createGlobalStyle } from 'styled-components'
import colors from './constants/colors'

const GlobalStyle = createGlobalStyle`
  html, body {
    height: 100vh;
    width: 100vw;
  }

  html {
    font-family: 'Roboto', sans-serif;
  }

  strong {
    font-weight: 700;
  }

  em {
    font-style: italic;
  }

  u {
    text-decoration: none;
    border-bottom: 0.3rem solid ${colors.primaryHoverColor};

    padding-bottom: 0.1rem;
  }

  .quill {
    background-color: ${colors.editorBgColor};
    flex-grow: 1;
    
    display: flex;
    flex-direction: column;

    box-shadow: ${colors.shadow};
  }

  .ql-toolbar, .ql-container {
    border-left: 0px !important;
    border-right: 0px !important;
    font-family: Roboto !important;
  }


  #root {
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to bottom, ${colors.gradientColor} 0%, ${colors.gradientColor} 13%, ${colors.gradientColor} 13%, ${colors.gradient2Color} 13%, ${colors.gradient2Color} 100%);
    width: 100%;
    height: 100%;
    padding: 3% 21%;
  }
`

ReactDOM.render(
  <React.StrictMode>
    <GlobalStyle />
    <Router>
      <App />
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);

