import React, { useState, useEffect } from 'react'
import styled from 'styled-components'

import {
  Switch, Route, useHistory, useRouteMatch
} from 'react-router-dom'

import CalendarPage from './pages/CalendarPage'
import JournalPage from './pages/JournalPage' 

import linkGenerator from './utils/linkGenerator'
import userService from './services/user'

const AppStyled = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
`

const App = () => {
  const [date, setDate] = useState(new Date())

  const history = useHistory()
  const goToJournalPage = (day) => {
    const newDate = new Date(date.getFullYear(), date.getMonth(), day)
    changeDate(newDate)

    const location = {
      pathname: linkGenerator.genEntryLink(newDate),
      state: { fromCalendar: true }
    }
    history.push(location)
  }

  const logout = () => {
    userService.logout()
    history.push('/auth')
  }

  const match = useRouteMatch('/:date')
  let journalDate = date
  if (match && !history.location.state) {
    const dateRaw = match.params.date.split('-')
    journalDate = new Date(
      parseInt(dateRaw[0]),
      parseInt(dateRaw[1]) - 1,
      parseInt(dateRaw[2])
    )
  }

  const isUpdatable = journalDate !== date
  useEffect(() => {
    setDate(journalDate)
  }, [isUpdatable])

  const changeDate = (newDate) => {
    setDate(newDate)
  }

  return (
    <AppStyled>
      <Switch>
        <Route exact={true} path="/">
          <CalendarPage 
            date={date} 
            changeDate={changeDate}
            goToJournalPage={goToJournalPage}
            logout={logout}
          />
        </Route>
        <Route path="/entry/:date">
          <JournalPage date={journalDate}/>
        </Route>
      </Switch>
    </AppStyled>
  )
}

export default App
