import React from 'react'

import { NavigationButton, NavigationSpacer, NavigationBar, SideWrapper } from './shared/NavigationElements'
import DateNavigaton from './DateNavigation' 

const CalendarBar = ({ date, changeDate, logout }) => {

  const openSettings = (e) => {
    e.preventDefault()

    logout()
  }

  return (
    <NavigationBar>
      <SideWrapper>
        <NavigationSpacer>settings</NavigationSpacer>
      </SideWrapper>
      <DateNavigaton date={date} changeDate={changeDate}/>
      <SideWrapper>
        <NavigationButton onClick={openSettings}>settings</NavigationButton>
      </SideWrapper>
    </NavigationBar>
  )
}

export default CalendarBar