import React from 'react'
import styled from 'styled-components'

import { NavigationButton } from './shared/NavigationElements' 
import colors from '../constants/colors'

const DateNavigatonStyled = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  margin: 0 auto;
`
const DateWrapper = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: baseline;
  margin: 0 auto;

  min-width: 15rem;
`

const Month = styled.span`
  font-weight: bold;
  color: ${colors.navbarElemColor};
  font-size: 1.7rem;
  margin-right: 0.5rem;
`

const Year = styled.span`
  font-weight: bold;
  color: ${colors.navbarElemColor};
  font-size: 1rem;
`

const DateNavigaton =  ({ date, changeDate }) => {
  const changeMonth = (e, operation) => {
    e.preventDefault()

    const newDate = new Date(date.getFullYear(), date.getMonth() + operation, date.getDate())
    
    changeDate(newDate)
  }

  return (
    <DateNavigatonStyled>
      <NavigationButton onClick={e => changeMonth(e, -1)}>chevron_left</NavigationButton>
      <DateWrapper>
        <Month>{date.toLocaleString('default', { month: 'long' })}</Month>
        <Year>{date.getFullYear()}</Year>
      </DateWrapper>
      <NavigationButton onClick={e => changeMonth(e, 1)}>chevron_right</NavigationButton>
    </DateNavigatonStyled>
  )
}

export default DateNavigaton